

import Vue from 'vue'
import App from './App'
import VueJsonp from 'vue-jsonp'
import VueScroller from 'vue-scroller'
import ToastPlugin from 'vux/src/plugins/toast/index.js'



Vue.config.productionTip = false

Vue.use(VueJsonp)
Vue.use(VueScroller)
Vue.use(ToastPlugin)

/* eslint-disable no-new */
new Vue({
el: '#app',
template: '<App/>',
components: { App }
})



