
/*Scroller as Sc  as就是起了一个别名*/
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sc from 'vux/src/components/scroller/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


const refreshKey = ['A', 'B01', 'B02', 'B03', 'B04', 'B05', 'B06', 'B07', 'B08', 'B09'];
let key = -1;
function getRefreshKey(){
key++;
if(key == refreshKey.length){
key = 0;
}
return refreshKey[key];
};

let start = 0;
let end = start + 9;
let bokLoaded = false;
let bok1 = false;     


export default {
name: 'app',
data(){
return {
swiperList:[],
swiperIndex:1,
panelList:[],
marqueeList:[],
morePanelList:[]
}
},
created(){


this.$jsonp('http://3g.163.com/touch/jsonp/sy/recommend/0-9.html').then( data => {

/*
箭头函数里面的this是定义环境的this,也就是vue实例，
获取轮播图数据,先过滤addata===null的项，然后map处理，返回包含url,img,title三个属性的对象
*/
this.swiperList = data.focus.filter( item => {
return item.addata === null;
}).map( item => {
return {
url: item.link,
img: item.picInfo[0] ? item.picInfo[0].url : '',
title: item.title
};
});



this.panelList = data.list.filter( item => {
return item.addata === null;
}).map( item => {
return {
url: item.link,
src: item.picInfo[0] ? item.picInfo[0].url : '',
title: item.title,
desc: item.digest
};
});



this.marqueeList = data.news.filter( item => {
return item.addata === null;
}).map( item => {
return {
content: item.title
};
});

bokLoaded = true;
bok1 = false;
});
},
components: {
ViewBox,
XHeader,
Tab,
TabItem,
Sc,
Swiper,
Marquee,
MarqueeItem,
Panel,
Tabbar,
TabbarItem
},
methods:{
clickHandle(ev){
alert(ev)
},
clickHandle2(ev){
alert(ev)
},
refresh(){


this.$jsonp('http://3g.163.com/touch/jsonp/sy/recommend/0-9.html',{
miss : '00',
refresh : getRefreshKey()
}).then( data => {

this.panelList = data.list.filter( item => {
return item.addata === null;
}).map( item => {
return {
url: item.link,
src: item.picInfo[0] ? item.picInfo[0].url : '',
title: item.title,
desc: item.digest
};
});

this.$refs.myRef.finishPullToRefresh();

/*
this.$vux.toast.show({
text:'刷新完成',
time:3000,
type:'success',
width:'7em',
position:'center'
});
*/
this.$vux.toast.text(`当前一共刷新了${this.panelList.length}条数据`,'top');
})
},
infinite(){
if(bok1){
return
};
bok1 = true;

if(!bokLoaded){//上来加载一次，初始化数据没有加载完成
this.$refs.myRef.finishInfinite();
return
}

this.$jsonp(`http://3g.163.com/touch/jsonp/sy/recommend/${start}-${end}.html`, {
miss: '00',
refresh: refreshKey[0]
}).then( data => {


setTimeout( () => {

const moreList = data.list.filter( item => {
return item.addata === null;
}).map( item => {
return {
url: item.link,
src: item.picInfo[0] ? item.picInfo[0].url : '',
title: item.title,
desc: item.digest
};
});


this.morePanelList = [...this.morePanelList,...moreList];
start += 10;
end = start + 9
this.$refs.myRef.finishInfinite();
bok1 = false;

},1000)


})


}
}
}



